.popuparticle {
    padding: 20px;
    display: grid;
    grid-template-rows: 1fr auto;
    grid-gap: 10px;

    position: fixed;
    z-index: 101;
    background-color: white;
    height: 60vh;
    width: 70vw;
    top: 15vh;
    left: 15vw;
}

.popuparticle > * {
    min-width: 0px;
    min-height: 0px;
}

.popupcontrol {
    display: flex;
    justify-content: space-between;
}

.popupclosebutton {
    position: absolute;
    width: 40px;
    background-color: none;
    border: none;
    border-radius: 0px;
    top: 0px;
    right: 0px;
    padding: 3px 0px 0px 0px;
}

.popupclosebutton:hover {
    background-color: rgb(254, 53, 53);
    color: white;
    padding: 3px 0px 0px 0px;
}

.popupclosebutton:hover img{
    filter: invert(100%);
}

.popupcover {
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 100;
    height: 100vh;
    width: 100vw;
    background-color: grey;
    opacity: 0.7;
}

.pop-up-article__content {
    display: flex;
    flex-direction: row;
    gap: 20px;

    overflow-y: auto;
    

    max-height: 100%;
    height: 100%;
    width: 100%;

}

.pop-up-article__content > * {
    min-height: 0px;
    min-width: 0px;
}

.pop-up-article__content img {
    height: 100%;
    max-width: 100%;
    object-fit: scale-down;
    flex: 0 0 auto;
}

.popuparticle div h2 {
    width: 90%;
}

.pop-up-article__content.pop-up-article__content__text { 
    max-width: auto;  
    min-width: 0px;
    min-height: 0px;
    padding: 0px;
    margin: 0px;
    height: 100%;
}

.pop-up-article__content p {
    padding: 0px;
    margin: 0px;
    padding-bottom : 10px;
}

.pop-up-article__upper-part {
    display: grid;
    grid-template-rows: auto 1fr;
    height: 100%;
    grid-gap: 10px; 
}

.pop-up-article__upper-part > * {
    min-height: 0px;
    min-width: 0px;
}

.pop-up__control_button {
    padding: 2px 10px;
}

.pop-up__contact_grid {
    display: grid;
    grid-template:
        "a a"
        "b c";
    gap: 10px;
}

.pop-up__contact_grid a {
    color: black;
    text-decoration: none;
}

.pop-up__contact_grid a:link { text-decoration: none; }
.pop-up__contact_grid a:visited { text-decoration: none; }
.pop-up__contact_grid a:hover { text-decoration: none; }
.pop-up__contact_grid a:active { text-decoration: none; }

.contact_grid_a {
    grid-area: a;
    background-color:var(--grey-01);;
    padding: 10px;
    border-radius: 10px;
    align-content: center;
    border: var(--element-border);
}

.contact_grid_b {
    grid-area: b;
    background-color:var(--grey-01);;
    padding: 10px;
    border-radius: 10px;
    text-align: center;
    border: var(--element-border);
}

.contact_grid_b:hover {
    background-color: var(--light-blue);
}
.contact_grid_c:hover {
    background-color: var(--light-blue);
}

.contact_grid_c {
    grid-area: c;
    background-color: var(--grey-01);
    padding: 10px;
    border-radius: 10px;
    text-align: center;
    border: var(--element-border);
}

.contact_grid_a p {
    padding: 0px;
    margin: 0px;
    text-align: center;
}

.about__tech_tag {
    display: flex;
    gap: 10px;
    flex-wrap:wrap;
    padding: 0px 0px 20px 0px;
}

.about__tech_tag div {
    padding: 15px;
    background: var(--grey-01);
    border-radius: 10px;
    border: var(--element-border);
}